:root {
  // Shadows
  --dialog-strong: 0px 16px 48px rgba(0, 0, 0, 0.24);
  --dialog: 0px 4px 8px rgba(0, 0, 0, 0.12);
  --content: 0px 2px 4px rgba(0, 0, 0, 0.08);
  --canvas: 0px 0px 0px rgba(0, 0, 0, 0);
  --keyboard-key: 0 2px 0px var(--grey4);

  // Focus
  --default: 0px 0px 0px 2px var(--blue5);
  --accent: 0px 0px 0px 2px var(--blue8);
  --inverse: 0px 0px 0px 2px var(--white);

  // Border widths
  --muted--width-01: inset 0px 0px 0px 1px var(--grey2);
  --muted--width-02: inset 0px 0px 0px 2px var(--grey2);
  --muted--width-04: inset 0px 0px 0px 4px var(--grey2);
  --width-01: inset 0px 0px 0px 1px var(--grey4);
  --width-02: inset 0px 0px 0px 2px var(--grey4);
  --width-04: inset 0px 0px 0px 4px var(--grey4);
  --strong--muted-width-01: inset 0px 0px 0px 1px var(--grey6);
  --strong--muted-width-02: inset 0px 0px 0px 2px var(--grey6);
  --strong--muted-width-04: inset 0px 0px 0px 4px var(--grey6);
  --bottom--muted-width-01: inset 0px -1px 0px var(--grey2);
  --bottom--width-01: inset 0px -1px 0px var(--grey4);
  --bottom--strong-width-01: inset 0px -1px 0px var(--grey6);
  --bottom--muted-width-02: inset 0px -2px 0px var(--grey2);
  --bottom--width-02: inset 0px -2px 0px var(--grey4);
  --botom--strong-width-02: inset 0px -2px 0px var(--grey6);
}